.container{
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(140deg, rgb(81, 8, 81), purple);

}

.calculator {
  padding: 20px;
  border-radius: 10px;
  background-color: white;
}

form input {
  border: none;
  outline: 0;
  width: 60px;
  height: 60px;
  font-size: 16px;
  background-color: rgb(195, 158, 190);
  margin: 2px;
  border-radius: 5px;
  font-weight: bolder;
  cursor: pointer;
}

form input[type="button"]:hover {
  background-color: purple;
  color:white;
}

form .display {
  display: flex;
  justify-content: flex-end;
  margin: 5px 0px 15px 0px;
}

form .display input{
  text-align: right;
  flex: 1; /* takes entire row */
  font-size: 40px;
  padding: 5px 10px;
  background-color: rgb(219, 187, 215);
}

form input.equal{
  width: 123px;
}